.footer_main {
    padding: 0 3rem;
    min-height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .footer_content {
        text-align: center;
        h1 {
            font-size: 6rem;
            color: $blanco;
            span {
                font-size: 7rem;
                display: block;
                color: $secundario;
            }
        }
        .footer_btn {
            margin-top: 5rem;
            border: 1px solid $blanco;
            color: $blanco;
            padding: 1rem 2.5rem;
            border-radius: .9rem;
            position: relative;
            background-color: transparent;
            transition: background-color 400ms;
            font-weight: 700;

            &:hover {
                background-color: $secundario;
                color: $blanco;
                cursor: pointer;
            }
        }
    }
}

.footer{
    img{
        max-width: 15rem;
        margin-bottom: 2rem;
    }
    text-align: center;
    margin-bottom: -10rem;
}