.banner_main{
    margin: 8rem 1rem 0rem 1rem;
    min-height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .banner_container{
        @include tablet {
            display: grid;
            grid-template-columns: repeat(2,1fr);
        }
        .banner_text{
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            .banner_title{
                margin-bottom: 1rem;
                h1{
                    color: $blanco;
                    font-size: 3.8rem;
                    text-align: center;
                    span{
                        display: block;
                        font-size: 5.4rem;
                        color: $secundario;
                    }
                }
            }
            .banner_btn {
                margin-top: 5rem;
                border: 1px solid $blanco;
                color: $blanco;
                padding: 1rem 2.5rem;
                border-radius: .9rem;
                position: relative;
                background-color: transparent;
                transition: background-color 400ms;
                font-weight: 700;
    
                &:hover {
                    background-color: $secundario;
                    color: $blanco;
                    cursor: pointer;
                }
            }
        }
        .banner_video{
            margin: 7rem .2rem;
            @include tablet{
                padding: 0 4rem;
                border-radius: 1.5rem;
            }
            video{
                border-radius: 1.5rem;
                border: solid 4px $secundario;

                &:hover{
                    cursor: pointer;
                }
            }
        }
    }
}