.viviendavis_main {
    display: flex;
    justify-content: center;
    .viviendavis_container {
        display: grid;
        justify-items: center;
        @include tablet {
            grid-template-columns: 1fr 2fr;
            align-items: center;
        }

        overflow: hidden;
        .viviendavis_imgs {
            @include tabletinvert{
                display: flex;
                justify-content: center;
            }
            order: 1;
            @include tablet{
                order: 2;
            }
            .imgs_content {
                @include tabletinvert{
                    max-width: 55%;
                }
                @include tablet{
                    position: relative;
                    right: -15rem;
                    img {
                        margin: -3rem 0;
                        width: 90%;
                        min-width: 490px;
                        max-width: 55rem;
                    }
                }
            }
        }

        .viviendavis_text {
            order: 2;
            @include tablet{
                order: 1;
            }
            text-align: center;
            padding: 0 2rem;
            max-width: 74rem;
            @include tablet {
                margin-left: 7rem;
                padding: 0 10rem;
                min-width: 70rem;
            }

            .viviendavis_content {
                .viviendavis_tilte {
                    h1 {
                        color: $blanco;

                        span {
                            color: $secundario;
                            display: block;
                            font-size: 7rem;
                        }
                    }

                    h2 {
                        color: $blanco;
                        font-size: 2rem;
                    }
                }

                .viviendavis_paragraf {
                    margin-top: 2rem;
                    p {
                        color: $blanco;
                        font-size: 1.7rem;
                        font-weight: 400;
                        line-height: 2.1rem;
                        text-shadow: 0px 0px 4px #000000;
                    }
                    .paragraf_tow {
                        color: $secundario;
                    }
                }

                .viviendavis_list {
                    margin-top: 5rem;
                    margin-bottom: 5rem;
                    @include tablet {
                        margin-top: 5rem;
                        margin-bottom: 1rem;
                    }
                    ul {
                        display: flex;
                        justify-content: space-around;
                        li {
                            button {
                                text-shadow: 0px 0px 4px #000000;
                                border: 1px solid $blanco;
                                color: $blanco;
                                padding: 2rem 2.5rem;
                                border-radius: 2rem;
                                position: relative;
                                background-color: transparent;
                                transition: background-color 400ms;
                                font-weight: 700;

                                &:hover {
                                    text-shadow: none;
                                    background-color: $secundario;
                                    color: $blanco;
                                    cursor: pointer;
                                }
                            }
                            span {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
}
