.viviendanovis_main {
  min-height: 100vh;
  display: flex;
  align-items: center;

  svg:last-child {
    margin-top: -0.6rem;
  }

  .caja_blanca {
    width: 100%;
    margin-top: -.8rem;
    background-color: $blanco;

    .viviendanovis_imgs {
      @include tabletinvert {
        display: flex;
        justify-content: center;
      }
      z-index: 20;
      margin: 0 1rem;
      @include tablet {
        margin: 0;
        margin-left: -15rem;
      }
      .imgs_content {
        @include tabletinvert {
          max-width: 55%;
        }
        @include tablet {
          margin-top: -2rem;
          margin-bottom: -4rem;
        }
        max-width: 55rem;
      }
      img,
      source {
        @include tablet {
          min-width: 95%;
        }
      }
    }

    @include tablet {
      display: grid;
      grid-template-columns: 1fr 2fr;
    }

    .viviendanovis_text {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 0 4rem;
      text-align: center;
      .viviendanovis_content {
        @include tablet {
          padding: 0 13rem;
        }
        .viviendanovis_title {
          margin: 6rem 0 4rem 0;
          @include tablet {
            margin: 0;
          }
          h1 {
            color: $primario;
            font-weight: 600;
            font-size: 3rem;
          }
          span {
            color: $secundario;
            font-weight: 700;
            display: block;
            font-size: 6rem;
          }
          h2 {
            font-size: 2rem;
            color: $primario;
          }
        }

        .viviendanovis_paragraf {
          p:first-child {
            font-weight: 600;
          }
          p {
            font-size: 1.7rem;
            text-align: center;
            font-weight: 400;
            line-height: 2.1rem;
            color: lighten($color: $negro, $amount: 20);
          }
        }

        .viviendanovis_list {
          margin-top: 5rem;
          margin-bottom: 5rem;
          @include tablet {
            margin-top: 5rem;
            margin-bottom: 1rem;
          }
          ul {
            display: flex;
            justify-content: space-around;
            li {
              button {
                border: 1px solid $secundario;
                padding: 2rem 2.5rem;
                border-radius: 2rem;
                position: relative;
                background-color: transparent;
                transition: background-color 400ms;
                font-weight: 700;

                &:hover {
                  background-color: $secundario;
                  color: $blanco;
                  cursor: pointer;
                }
              }
              span {
                display: block;
              }
            }
          }
        }
      }
    }
  }
}
