.leasing_main{
    display: flex;
    align-items: center;
    position: relative;
    z-index: 100;

    
    .leasing_container{

        svg:last-child{
            margin-top: -.6rem;
        }

        .caja_blanca{
            margin-top: -.8rem;
            padding: 9rem 1rem;
            width: 100%;
            background-color: $blanco;
            display: grid;


            .leasing_text{

                display: flex;
                justify-content: flex-end;
                align-items: center;
                
                @include telefono{
                    justify-content: center;
                }

                @include tablet{
                    border-right: solid 2px $secundario;
                    margin-right: 13rem;
                }

                h1{
                    color: $primario;
                    margin-right: 3rem;
                }

                span{
                    display: block;
                    font-size: 5.9rem;
                    color: $secundario;
                }
            }

            .leasing_content{

                padding: 0 2rem;
                
                @include tablet{
                    padding-right: 14rem;
                    padding-left: 1rem;
                }

                .leasing_paragraf{
                    p{
                        font-size: 1.7rem;
                        text-align: center;
                        font-weight: 400;
                        line-height: 2.1rem;
                        color: lighten($color: $negro, $amount: 20);
                    }
                    span{
                        color: $primario;
                    }
                }

                .leasing_list{
                    margin-top: 3rem;
                    ul{
                        @include tablet{
                            display: flex;
                            column-gap: 4rem;
                        }
                        li{
                            text-align: center;
                            font-size: 1.6rem;
                            color: $secundario;
                            font-weight: bold;
                            margin-top: 1.5rem;
                            margin-bottom: 1.5rem
                        }
                    }
                }

            }

            @include tablet{
                @include grid(2);
                gap: $separacion / 4;
            }
    
        }
    }
}