body{
    background-image: url("https://leasingdavivienda.colraices.com/assets/img/fondo.webp");
    background-repeat: no-repeat;
    background-size: cover; 
    background-attachment: fixed;
    backdrop-filter: blur(1rem);
}



.header_main {
    margin-top: -5.9rem;
    padding-left: 2rem;
    padding-right: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    z-index: 5000;
    top: .7rem;
    
    .header_nav {
        
        background-color: $blancoTrans;
        border-radius: 1.7rem;
        padding: 1.2rem 2.5rem;
        display: none;
        @include tablet{
            display: flex;
        }
        @include tablet{
            flex-direction: row;
            max-height: 59px;
        }
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        &:hover > .header_links{
            display: block ;
        }

        .header_logos {
            min-width: 258px;
            max-width: 258px;
            display: grid;
            grid-template-columns: 2fr 1fr;
            
            img:first-child {
                padding-right: 2rem;
                border-right: solid 1px lighten($color: $negro, $amount: 70);
            }
            
            img:last-child {
                padding-left: 1rem;
            }
 
        }
        
        


    }
    .header_btn {
        border-radius: 1rem;
        background-color: lighten($color: $primario, $amount: 10);
        color: $blanco;
        font-weight: bold;
        padding: 1rem 2rem;
        border: none;
        font-size: 1.7rem;
        box-shadow: 0px 0px 4px 0px rgba(255,255,255,0.47);
        transition: transform 300ms;

        &:hover{
            cursor: pointer;
            transform: scale(1.02);
        }

        &:active{
            transform: scale(.98);
        }
    }

    .header_links { 
        // display: none; 
        transition: all 0.5s ease-out;
        animation-name: load;
        animation-duration: 20s;

        ul {
            display: flex;
            column-gap: 2rem;
            align-items: center;
            justify-content: center;
            font-size: 1.4rem;
            li{
                max-width: 15rem;
                text-align: center;
                a{
                    text-shadow: 0px 0px 1px $primario;
                    color: $primario;
                    &:hover{
                        text-shadow: 0px 0px 1px $secundario;
                        color: $secundario;
                    }
                }
            }
        }
        &:hover{
            display: block ;
        }
    }
}
